/* GLOBAL */
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format(woff2), url("../fonts/oswald/oswaldregular.woff") format(woff);
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format(woff2), url("../fonts/oswald/oswaldmedium.woff") format(woff);
  font-style: normal;
  font-weight: 500;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format(woff2), url("../fonts/lato/Lato-Regular.woff") format(woff);
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  background-color: #ffffff;
  color: #444444;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  margin: 0;
  padding: 0;
  min-height: 100%;
  min-width: 320px;
  display: flex;
  flex-direction: column;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
.button {
  background-color: transparent;
  font-family: inherit;
  border: none;
  cursor: pointer;
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  width: calc(100% - 40px);
  max-width: 1220px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper {
    width: calc(100% - 60px);
  }
}
@media (min-width: 1440px) {
  .wrapper {
    width: calc(100% - 220px);
  }
}
@media (min-width: 1440px) {
  .wrapper--pet-info {
    display: grid;
    grid-template-columns: 570px 1fr;
    margin-bottom: 69px;
  }
}
@media (min-width: 1440px) {
  .wrapper--contacts {
    display: grid;
    grid-template-columns: 570px 1fr;
    column-gap: 80px;
    margin-bottom: 73px;
  }
}
@media (min-width: 768px) {
  .wrapper--textarea {
    padding-right: 40px;
  }
}
@media (min-width: 1440px) {
  .wrapper--textarea {
    padding-right: 0;
    margin-bottom: 67px;
  }
}
.partnership {
  background-color: #f2f2f2;
}
@media (min-width: 1440px) {
  .partnership__wrapper {
    position: relative;
    height: 0;
    padding: 0;
  }
}
.partnership__content {
  display: flex;
  flex-wrap: wrap;
  gap: 17px;
  padding: 26px 0;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .partnership__content {
    padding: 60px 40px;
  }
}
@media (min-width: 1440px) {
  .partnership__content {
    width: 565px;
    padding: 60px 78px 60px 80px;
    position: absolute;
    z-index: 1;
    top: 95px;
    left: 0;
    background-color: #ffffff;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }
  .partnership__content--form {
    top: 95px;
  }
}
.partnership__title {
  margin: 0;
  color: #111111;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  max-width: 125px;
}
@media (min-width: 768px) {
