  font-style: normal;
  line-height: 20px;
  position: relative;
  max-width: 138px;
}
@media (min-width: 768px) {
  .partnership__address {
    font-size: 16px;
    line-height: 20px;
    text-align: right;
    max-width: 200px;
  }
}
@media (min-width: 1440px) {
  .partnership__address {
    text-align: left;
  }
}
@media (min-width: 768px) {
