.radio:focus-visible + .radio--custom::before {
  outline: 1px solid #444444;
}
.radio--custom {
  max-width: 100%;
  position: relative;
  display: flex;
  align-items: center;
  gap: 15px;
  padding-left: 45px;
}
.radio--custom::before {
  content: "";
  position: absolute;
  display: inline-block;
  top: -8px;
  left: 0;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
  width: 30px;
  height: 30px;
}
@media (min-width: 768px) {
  .radio--custom {
    gap: 19px;
    padding-left: 55px;
  }
  .radio--custom::before {
    width: 40px;
    height: 40px;
  }
}
.radio--custom:hover::before {
  border-color: #bababa;
}
.radio:disabled + .radio--custom {
  opacity: 0.3;
  border-color: #cdcdcd;
}
.radio:checked + .radio--custom::before {
  background-image: url("../img/radio-checked.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 12px 12px;
}
@media (min-width: 768px) {
  .radio:checked + .radio--custom::before {
    background-size: 18px 18px;
  }
}
.checkbox:focus-visible + .checkbox--custom::before {
  outline: 1px solid #444444;
}
.checkbox--custom {
  width: max-content;
  display: flex;
  align-items: center;
  gap: 15px;
  position: relative;
}
.checkbox--custom::before {
  content: "";
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  top: 0;
  left: 0;
  border: 1px solid #cdcdcd;
}
.checkbox--custom:hover::before {
  border-color: #bababa;
}
@media (min-width: 768px) {
  .checkbox--custom {
    gap: 20px;
  }
  .checkbox--custom::before {
    width: 40px;
    height: 40px;
  }
}
.checkbox:checked + .checkbox--custom::before {
  background-image: url("../img/checkbox-checked.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.checkbox:disabled + .checkbox--custom {
  cursor: default;
  opacity: 0.3;
}
